.button-5 {
    text-decoration: none;
    display: inline-block;
    position: relative;
    padding: 15px 30px;
    color: #043f1a;
    border: 1px solid white;
    font-size: 14px;
    font-weight: 700;
    white-space: nowrap;
    transition: .3s linear;
    background-color: WHITE;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.button-5:before, .button-5:after, .link-content:before, .link-content:after {
    content: "";
    position: absolute;
    z-index: 1;
    width: 15px;
    height: 15px;
    transition: .3s linear;
}
.button-5:before {
    left: -1px;
    top: -1px;
    border-left: 1px solid #91c788;
    border-top: 1px solid #91c788;
}
.button-5:after {
    bottom: -1px;
    right: -1px;
    border-bottom: 1px solid #91c788;
    border-right: 1px solid #91c788;
}
.link-content:after {
    right: -1px;
    top: -1px;
    border-right: 1px solid #91c788;
    border-top: 1px solid #91c788;
}
.link-content:before {
    left: -1px;
    bottom: -1px;
    border-bottom: 1px solid #91c788;
    border-left: 1px solid #91c788;
}
.button-5:hover {
    color:#91c788;
    border-color: #91c788;
}

