}
@-webkit-keyframes slide {
  0% {
    -webkit-transform: translateX(-25%);
            transform: translateX(-25%);
  }
  100% {
    -webkit-transform: translateX(25%);
            transform: translateX(25%);
  }
}
@keyframes slide {
  0% {
    -webkit-transform: translateX(-25%);
            transform: translateX(-25%);
  }
  100% {
    -webkit-transform: translateX(25%);
            transform: translateX(25%);
  }
}
.homepage-btn {
  color: #ffffff;
  background: #ffffff;
  border: 1px solid #4fba6f;
  position: relative;
  font-size: 16px;
  letter-spacing: 0.3em;
  font-family: "Montserrat", sans-serif;
  padding: 15px;
  transition: 0.2s ease-in-out;
  cursor: pointer;
  transition: all 0.3s;
  border-radius: 3px;
  width: 100%;
  text-align: center;
}

.homepage-btn::after {
  content: "";
  position: absolute;
  top: 5px;
  left: 5px;
  width: 100%;
  height: 100%;
  background: #4fba6f;
  z-index: 1;
  transition: 0.25s ease;
  /* transform: translate(0, 0); */
  border-radius: 3px;
}
.homepage-btn:hover::after {
  /* transform: translate(4px, 4px); */
  background: #d8ffd9;
  top: 0px;
  left: 0px;
}
.homepage-btn:hover span,
.homepage-btn:hover span input {
  color: #000000;
}
.homepage-btn span {
  position: relative;
  z-index: 2;
  transition: all 0.3s;
  display: block;
}
.homepage-btn span input {
  margin: -15px;
  margin: -15px 0px;
  width: 100%;
  padding: 15px;
  border: none;
  background: none;
  color: #ffffff;
  font-size: 16px;
}
input:focus {
  outline: none;
}
